import { cardTemplate, openImageHandler } from "../index.js";
export { deleteCard, createCard };

// @todo: Функция создания карточки

function createCard(card, deleteCallback, openImageHandler) {
  const cardElement = cardTemplate.querySelector(".card").cloneNode(true);
  const cardImages = cardElement.querySelector(".card__image");
  const cardTitle = cardElement.querySelector(".card__title");
  cardImages.alt = card.name;
  cardImages.src = card.link;
  cardTitle.textContent = card.name;

  cardImages.addEventListener("click", () => {
    openImageHandler(cardImages.src, cardImages.alt, cardTitle.textContent);
  });


  const likeBtn = cardElement.querySelector(".card__like-button");
  likeBtn.addEventListener("click", toggleLike);

  return cardElement;
}

// @todo: Функция удаления карточки
function deleteCard(card, cardElement, deleteCallback, currentUserID) {
  cardElement.querySelector('.card__delete-button').addEventListener('click', function() {
    if(card.owner._id === currentUserID) {
      deleteCallback(cardElement)
    } else {
      console.log("Вы не можете удалить эту карточку, так как вы не ее создатель.");
    }
  })
  // cardElement.remove();
}

// let likeCount = 0;

function toggleLike(evt) {
  const likeButton = evt.currentTarget;
  const likeCountSpan = likeButton.nextElementSibling;

  const card = likeButton.closest(".card");

  let likeCount = parseInt(card.getAttribute("data-like-count")) || 0;

  likeButton.classList.toggle("card__like-button_is-active");

  if (likeButton.classList.contains("card__like-button_is-active")) {
    likeCount++;
  } else {
    likeCount--;
  }

  card.setAttribute("data-like-count", likeCount.toString());

  if (likeCount === 0) {
    likeCountSpan.textContent = "";
  } else {
    likeCountSpan.textContent = likeCount.toString();
  }
}
